.itemPage__section--figures{
    flex-basis: 100%;
    display: flex;
    flex-wrap: wrap;

    .figures__mini{
        order: 1;
        flex-basis: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;

        figure{
            flex-basis: 30%;
            margin: 1.5%;
        }
    }

        .figures__main{
            flex-basis: 100%;
            order: 0;
        }
}

@media (min-width: 600px) {
    .itemPage__section--figures{
        flex-wrap: nowrap;

        .figures__mini{
            order: 0;
            flex-basis: 15%;
            display: block;

            figure{
                margin: 0.2em;
                height: fit-content;
            }
        }
                
        .figures__main{
            order: 1;
            flex-basis: 80%;
        }
    }
}